.promo {
  max-width: 1194px;
  margin: auto;
  padding-top: 90px;
  display: flex;
  overflow-x: hidden;

  @media (max-width: 768px) {

    &::before {
      content: url(../images/productImgSmall.svg);
    }
  }

  &__text {
    width: 30%;
    padding-top: 81px;
  }

  &__title {
    font-family: "DIN Pro", sans-serif;
    font-size: 64px;
    font-weight: bold;
    line-height: 130%;
    text-transform: uppercase;

  }

  &__description {
    font-family: "HalveticaNeue", sans-serif;
    padding-top: 27px;
    font-size: 16px;
    line-height: 24px;
    color: #333;
  }

  &__img {
    z-index: -1;
    background: url(../images/a347db2baba6b808980fb0422ff7f7f2_original-1.svg);
    background-position: left;
    background-repeat: no-repeat;
    width: 900px;
    height: 464px;
    overflow: hidden;
  }
  @media (max-width: 1150px) {
    justify-content: space-between;
  
    &__img {
      width: 600px;
    }
  }
  
  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
    padding-top: 22px;
  
    &__text {
      padding-top: 40px;
      width: 90%;
    }
  
    &__title {
      text-align: center;
      font-size: 44px;
    }
  
    &__description {
      text-align: center;
    }
  
    &__img {
      display: none;
    }
  }
}
